
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

export default {
data: function () {
return {
mobile: '',
password: '',
code: null,
sended: false,
itemIndex: 0,
second: 60
}
},
mounted () {
},
methods: {
onItemClick: function (index) {
this.itemIndex = index
},
requestCode: function () {
if (this.$isEmptyParam(this.mobile)) {
this.$vux.toast.text('请输入手机号')
return false
}
if (!this.$checkMobileFormat(this.mobile.replace(/\s/g, ''))) {
this.$vux.toast.text('请正确输入手机号')
return false
}
this.$postData(this.$configs.api.sms, { mobile: this.mobile.replace(/\s/g, '') }, response => {
if (response) {
this.sended = !this.sended
this.countDown()
this.$vux.toast.text('发送成功')
}
})
},
countDown: function () {
setInterval(() => {
if (this.second < 1) {
this.sended = false
this.second = 60
return
}
this.second--
}, 1000)
},
submit: function submit () {
const currentRoutePath = localStorage.getItem('currentRoutePath')
const data = {}
if (this.$isEmptyParam(this.mobile)) {
this.$vux.toast.text('请输入手机号')
return false
}
if (!this.$checkMobileFormat(this.mobile.replace(/\s/g, ''))) {
this.$vux.toast.text('请正确输入手机号')
return false
}
if (this.itemIndex === 0) {
if (this.$isEmptyParam(this.code)) {
this.$vux.toast.text('请输入验证码')
return false
}
data.code = this.code
data.status = 1
} else if (this.itemIndex === 1) {
if (this.$isEmptyParam(this.password)) {
this.$vux.toast.text('请输入密码')
return false
}
if (this.password.length > 12 || this.password.length < 4) {
this.$vux.toast.text('请输入4-12位密码')
return false
}
data.password = this.password
data.status = 2
}
data.mobile = this.mobile.replace(/\s/g, '')
this.$postData(this.$configs.api.login, data, response => {
if (response) {
localStorage.token = response.token
this.$vux.toast.text('登陆成功')
if (!this.$isEmptyParam(currentRoutePath)) {
setTimeout(() => {
this.$router.push(currentRoutePath)
}, 1500)
} else {
setTimeout(() => {
this.$router.push({ name: 'Index' })
}, 1500)
}
}
})
}
},
components: {
Group,
XInput,
XButton,
Flexbox,
FlexboxItem,
Divider,
Tab,
TabItem
}
}



